.App {
  max-width: 600px;
  margin: auto;
  padding: 1rem;
  font-family: sans-serif;
  text-align: center;
}

.search-bar {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.search-bar input {
  flex: 1;
  padding: 0.5rem;
}

.search-bar button {
  padding: 0.5rem 1rem;
}

.current-weather {
  border: 1px solid #ddd;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.forecast {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.forecast-card {
  border: 1px solid #ddd;
  padding: 0.5rem;
  border-radius: 6px;
}

.history ul {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.history button {
  background: none;
  border: 1px solid #007acc;
  padding: 0.3rem 0.6rem;
  border-radius: 4px;
  cursor: pointer;
}

.history button:hover {
  background: #e6f7ff;
}
